
import 'styles/index.scss';



document.addEventListener('DOMContentLoaded', function(){

  const menu = document.getElementById('b-menu');
  const button = document.getElementById('b-button-icon');
  let iconMenu = document.getElementById('b-button-icon').src;
  let toggle = false;

  let locationSite = window.location.href;
  let imgSrc;
  let imgSrc2;
  if (locationSite.includes("local")){
    console.log("localhost")
    imgSrc = './../assets/images/burger.svg';
    imgSrc2 = './../assets/images/close.svg';
  } else {
    imgSrc = './assets/images/burger.svg';
    imgSrc2 = './assets/images/close.svg';
    console.log("not local")
  }

  function classToggle() {
    menu.classList.toggle('b-close-menu');
    if (toggle === true) {
               document.getElementById('b-button-icon').src  = imgSrc;
        }
         else {
               document.getElementById('b-button-icon').src =  imgSrc2;
       }
    toggle = !toggle;
  }
  button.addEventListener('click', classToggle);

  let imgBanner = document.getElementsByClassName('b-banners__img')[0];


 if (window.matchMedia("(max-width: 420px)").matches){
  imgBanner.src= "./../assets/images/bg-mobile.png"
  imgBanner.srcset="./../assets/images/bg-mobile.png 2x, ./../assets/images/bg-mobile.png 3x"
  // console.log("mobile")
 }
 if (window.matchMedia("(min-width: 420px)").matches){
    imgBanner.src= "./../assets/images/bitmap.jpg"
    imgBanner.srcset="./../assets/images/bitmap@2x.jpg 2x, ./../assets/images/bitmap@3x.jpg 3x"
    // console.log("not phone")
 }



  window.addEventListener("resize", resizeThrottler, false);

  var resizeTimeout;
  function resizeThrottler() {
    if ( !resizeTimeout ) {
      resizeTimeout = setTimeout(function() {
        resizeTimeout = null;
        actualResizeHandler();
       }, 66);
    }
  }

  function actualResizeHandler() {
    console.log("resize");
      if (window.matchMedia("(max-width: 420px)").matches){
        imgBanner.src= "./assets/images/bg-mobile.png"
        imgBanner.srcset="./assets/images/bg-mobile.png 2x, ./assets/images/bg-mobile.png 3x"
      }
      if (window.matchMedia("(min-width: 420px)").matches){
          imgBanner.src= "./assets/images/bitmap.jpg"
          imgBanner.srcset="./assets/images/bitmap@2x.jpg 2x, ./assets/images/bitmap@3x.jpg 3x"
      }
  }

});
